<template>
  <div class="user-info">
    <h1></h1>
    <el-form>
      <section>
        <h2></h2>
        <p>{{ me.name }}</p>
        <p>{{ me.roleName}}</p>
        <p v-if="me.company">{{ me.company.name}}</p>
        <p v-if="me.soItem"> {{ me.soItem.number}}</p>
      </section>
      <section>
        <h2></h2>
          <el-form-item label="">
            <el-input
              :value="me.phone"
              @change="value => this.updateMe({ phone: value })"
              placeholder="">
            </el-input>
          </el-form-item>
          <el-form-item label="email">
            <el-input
              :value="me.email"
              @change="value => this.updateMe({ email: value })"
              placeholder=" email">
            </el-input>
          </el-form-item>
      </section>
      <section>
        <h2></h2>
        <el-form-item label="">
          <el-input
            :value="me.password"
            @change="value => this.updateMe({ password: value })"
            placeholder="">
          </el-input>
        </el-form-item>
      </section>
      <section>
        <h2></h2>
        <el-table
          :data="me.projects"
          style="width: 100%">
          <el-table-column
            prop="id"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="name"
            label="">
          </el-table-column>
        </el-table>
      </section>
      <div class="button-container">
        <el-button type="primary" @click="submit">
          
        </el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
// @ is an alias to /src
import ToPathMixin from '@/mixins/ToPath'

export default {
  name: 'UserInfo',
  mixins: [ToPathMixin],
  data() {
    return {
    }
  },
  computed: {
    me () {
      return this.$store.getters.me
    }
  },
  methods: {
    updateMe(payload) {
      this.$store.commit('updateMe', payload)
    },
    submit () {
      this.$store.dispatch('updateMe').then(() => {
        this.$message({ message: '', type: 'success' })
        this.toPath('ProjectList')
      }).catch(e => {
        this.$message.error('')
      })
    }
  }
}
</script>
